import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { BarChart3, Network, FileText, Download, RefreshCw } from 'lucide-react';
import Plot from 'react-plotly.js';
import axios from 'axios';
import toast from 'react-hot-toast';

const ResultsPage = () => {
  const { analysisId } = useParams();
  const [results, setResults] = useState(null);
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState('overview');

  useEffect(() => {
    fetchResults();
  }, [analysisId]);

  const fetchResults = async () => {
    try {
      const response = await axios.get(`/api/results/${analysisId}`);
      setResults(response.data);
    } catch (error) {
      console.error('Error fetching results:', error);
      toast.error('Failed to load results');
    } finally {
      setLoading(false);
    }
  };

  const downloadResults = async (format = 'json') => {
    try {
      const response = await axios.get(`/api/download/${analysisId}`, {
        params: { format },
        responseType: 'blob'
      });
      
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', `analysis_results.${format}`);
      document.body.appendChild(link);
      link.click();
      link.remove();
      
      toast.success('Results downloaded successfully!');
    } catch (error) {
      toast.error('Failed to download results');
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-96">
        <div className="text-center">
          <RefreshCw className="w-8 h-8 animate-spin mx-auto mb-4 text-primary-600" />
          <p className="text-gray-600">Loading analysis results...</p>
        </div>
      </div>
    );
  }

  if (!results) {
    return (
      <div className="text-center py-12">
        <p className="text-gray-600">No results found for this analysis.</p>
      </div>
    );
  }

  const tabs = [
    { id: 'overview', label: 'Overview', icon: BarChart3 },
    { id: 'embeddings', label: 'Embeddings', icon: BarChart3 },
    { id: 'entities', label: 'Entities', icon: FileText },
    { id: 'network', label: 'Network', icon: Network },
  ];

  return (
    <div className="max-w-7xl mx-auto">
      {/* Header */}
      <div className="flex items-center justify-between mb-8">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Analysis Results</h1>
          <p className="text-gray-600">Analysis ID: {analysisId}</p>
        </div>
        
        <div className="flex items-center space-x-4">
          <button
            onClick={() => downloadResults('json')}
            className="btn-secondary flex items-center"
          >
            <Download className="w-4 h-4 mr-2" />
            Download JSON
          </button>
          <button
            onClick={() => downloadResults('csv')}
            className="btn-secondary flex items-center"
          >
            <Download className="w-4 h-4 mr-2" />
            Download CSV
          </button>
        </div>
      </div>

      {/* Tabs */}
      <div className="border-b border-gray-200 mb-8">
        <nav className="-mb-px flex space-x-8">
          {tabs.map((tab) => {
            const Icon = tab.icon;
            return (
              <button
                key={tab.id}
                onClick={() => setActiveTab(tab.id)}
                className={`flex items-center py-2 px-1 border-b-2 font-medium text-sm ${
                  activeTab === tab.id
                    ? 'border-primary-500 text-primary-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }`}
              >
                <Icon className="w-4 h-4 mr-2" />
                {tab.label}
              </button>
            );
          })}
        </nav>
      </div>

      {/* Tab Content */}
      <div className="space-y-8">
        {activeTab === 'overview' && (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
            <div className="card text-center">
              <div className="text-2xl font-bold text-primary-600 mb-2">
                {results.stats?.total_documents || 0}
              </div>
              <div className="text-sm text-gray-600">Documents Analyzed</div>
            </div>
            
            <div className="card text-center">
              <div className="text-2xl font-bold text-green-600 mb-2">
                {results.stats?.total_entities || 0}
              </div>
              <div className="text-sm text-gray-600">Entities Found</div>
            </div>
            
            <div className="card text-center">
              <div className="text-2xl font-bold text-purple-600 mb-2">
                {results.stats?.num_communities || 0}
              </div>
              <div className="text-sm text-gray-600">Communities</div>
            </div>
            
            <div className="card text-center">
              <div className="text-2xl font-bold text-orange-600 mb-2">
                {results.stats?.avg_degree?.toFixed(2) || 0}
              </div>
              <div className="text-sm text-gray-600">Avg. Degree</div>
            </div>
          </div>
        )}

        {activeTab === 'embeddings' && results.embeddings && (
          <div className="space-y-6">
            <div className="card">
              <h3 className="text-lg font-semibold mb-4">Document Embeddings Visualization</h3>
              <div className="h-96">
                <Plot
                  data={results.embeddings.plot?.data || []}
                  layout={results.embeddings.plot?.layout || {}}
                  style={{ width: '100%', height: '100%' }}
                />
              </div>
            </div>
            
            {results.embeddings.clusters && (
              <div className="card">
                <h3 className="text-lg font-semibold mb-4">Document Clusters</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  {Object.entries(results.embeddings.clusters).map(([filename, cluster]) => (
                    <div key={filename} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                      <span className="font-medium">{filename}</span>
                      <span className="px-2 py-1 bg-primary-100 text-primary-700 rounded text-sm">
                        Cluster {cluster}
                      </span>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        )}

        {activeTab === 'entities' && results.entities && (
          <div className="space-y-6">
            <div className="card">
              <h3 className="text-lg font-semibold mb-4">Entity Analysis</h3>
              <div className="h-96">
                <Plot
                  data={results.entities.visualizations?.entity_plot?.data || []}
                  layout={results.entities.visualizations?.entity_plot?.layout || {}}
                  style={{ width: '100%', height: '100%' }}
                />
              </div>
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="card">
                <h4 className="font-semibold mb-3">Entity Types</h4>
                <div className="space-y-2">
                  {Object.entries(results.entities.entity_counts || {}).map(([type, count]) => (
                    <div key={type} className="flex justify-between items-center">
                      <span className="text-sm">{type}</span>
                      <span className="px-2 py-1 bg-gray-100 rounded text-sm">{count}</span>
                    </div>
                  ))}
                </div>
              </div>
              
              <div className="card">
                <h4 className="font-semibold mb-3">Top Entities by Importance</h4>
                <div className="space-y-2">
                  {Object.entries(results.entities.importance_scores || {})
                    .sort(([,a], [,b]) => b - a)
                    .slice(0, 10)
                    .map(([entity, score]) => (
                      <div key={entity} className="flex justify-between items-center">
                        <span className="text-sm truncate">{entity}</span>
                        <span className="px-2 py-1 bg-primary-100 text-primary-700 rounded text-sm">
                          {score.toFixed(3)}
                        </span>
                      </div>
                    ))}
                </div>
              </div>
            </div>
          </div>
        )}

        {activeTab === 'network' && results.network && (
          <div className="space-y-6">
            <div className="card">
              <h3 className="text-lg font-semibold mb-4">Network Visualization</h3>
              <div className="h-96">
                <iframe
                  src={`/static/networks/${results.network.network_path}`}
                  className="w-full h-full border-0 rounded-lg"
                  title="Network Visualization"
                />
              </div>
            </div>
            
            <div className="card">
              <h3 className="text-lg font-semibold mb-4">Network Analysis</h3>
              <div className="h-96">
                <Plot
                  data={results.network.visualizations?.network_plot?.data || []}
                  layout={results.network.visualizations?.network_plot?.layout || {}}
                  style={{ width: '100%', height: '100%' }}
                />
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default ResultsPage;

